$form-color: #ffccff;
$secondary-color: #ff99ff;

fieldset {
	color: $form-color;
	width: 60%;
	margin: 2rem auto;
	padding: 1rem;
	border: none;
}

legend {
	text-align: center;
	font-weight: bold;
	text-shadow: 1px 1px 2px $secondary-color; 
	padding: 1rem;
}

label {
	display: block;
	color: $form-color;
	margin-top: 1rem;
}

input {
	border: none;
	border-bottom: 1px solid $form-color;
	border-radius: 10px;
	padding: 0.5rem;
	display: block;
	width: 60%;
	font-size: 1.2rem;
	font-family: 'Thasadith';
}

select {
	-moz-appearance: none;
	text-indent: 0.01px;
	text-overflow: '';
	width: 60%;
	border: none;
	padding: 1rem;
	font-family: 'Thasadith';
	font-size: 1.2rem;
}


input[type="button"] {
	background-color: $form-color;
	width: 20%;
	margin-top: 1.5rem;
	border-radius: 8px;
	border-color: $form-color;
	font-family: 'Thasadith';
	color: #fff;
	font-weight: bold;
	font-size: 1rem;
}