@import 'css/_form.scss';
@import 'css/_table.scss';

$font-stack: Thasadith, sans serif;
$primary-color: #fff;
$secondary-color: #ff99ff;

* {
  box-sizing: border-box;
  outline: none;
}

body {
  margin: 0;
  font: 100% $font-stack;
  line-height: 1.6rem;
  font-size: 1.5rem;
}

h1 {
  color: $primary-color;
  text-shadow: 1px 1px 2px $secondary-color;
}

h2,
h3,
h4,
h5,
h6 {
  color: $secondary-color;
  font-weight: 700;
  text-align: center;
}

img {
  max-width: 100%;
}

header {
  text-align: center;
  background-image: url('header.jpg');
  background-size: cover;
  background-position: top 75% right 20%;
  padding: 200px;
}

p { line-height: 2rem;
}

button {
  color: $primary-color;
  font: 100% $font-stack;
  background-color: $secondary-color;
  border-radius: 8px;
  border-color: $secondary-color;
  padding: 0.5rem;
}

@media (min-width: 300px) and (max-width: 1000px) {

  body {
    font: 70% $font-stack;
    line-height: 1.2rem;
  }

  header {
    padding: 50px;
  }

  button {
    font: 80% $font-stack;
  }

  fieldset,
  input,
  select 
  {
    margin: 0;
    width: 100%;
    font: 70% $font-stack;
    padding: 0.5rem;
  }

  input[type="button"] {
    font: 80% $font-stack;
  }

  table {
    width: 100%;
  }
}