$dominant-color: #ff99ff;
$content-color: #003399;

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 80%;
  max-width: 100%;
  margin: 0 auto; }


thead th {
  border-bottom: 2px solid $dominant-color; }

tfoot th {
  border-top: 2px solid $dominant-color; }

td {
  border-bottom: 1px solid $dominant-color; }

th,
td {
  text-align: left;
  padding: .5rem;
  color: $content-color; }


